@import url(//fonts.googleapis.com/css?family=Montserrat:300,400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{height:100%;margin:0;font-family:Montserrat}.app{-webkit-scroll-snap-type:x mandatory;-ms-scroll-snap-type:x mandatory;scroll-snap-type:x mandatory;overflow-x:scroll;display:flex;max-height:100vh;max-width:100vw;overflow-y:hidden}.app section{max-height:100vh;overflow-y:scroll;min-width:50vw;display:flex;scroll-snap-align:start}.title{margin-bottom:1.5rem;text-align:center;font-size:5em;font-weight:100;color:white;letter-spacing:0.5rem}@media (max-width: 600px){body{font-size:14px}.app{-webkit-scroll-snap-type:x mandatory;-ms-scroll-snap-type:x mandatory;scroll-snap-type:x mandatory;overflow-x:unset;display:block;max-height:100vh;max-width:unset;overflow-y:scroll;flex-direction:row}.app section{max-width:100vw;min-width:100vw;max-height:unset}.title{margin-bottom:1.5rem;text-align:center;font-size:2.5rem;font-weight:100;color:white;letter-spacing:0.5rem}}

.experience-section{background-color:#992f15;color:#fbffe5;padding:20px 0}.timeline{width:80%;padding:50px 0;margin:0 auto;position:relative;overflow:hidden}.timeline:before{content:"";position:absolute;top:0;left:50%;margin-left:-1px;width:2px;height:100%;background:#ee964b;z-index:1}.timeline .timeline-block{width:calc(50% + 8px);display:flex;justify-content:space-between;clear:both}.timeline .timeline-block .marker{min-width:16px;max-width:16px;min-height:16px;max-height:16px;border-radius:50%;border:2px solid #fcffa8;background:#fcffa8;margin-top:10px;z-index:9999}.timeline .timeline-block .timeline-content{width:95%;padding:0 15px}.timeline .timeline-block .timeline-content ul{margin:0;padding:0}.timeline .timeline-block .timeline-content ul li{list-style:none;margin:10px 0;padding:0}.timeline .timeline-block .timeline-content h3{margin-top:5px;margin-bottom:5px;font-size:2rem;font-weight:100;letter-spacing:0.5rem}.timeline .timeline-block .timeline-content span{font-size:15px}.timeline .timeline-block .timeline-content p{font-size:1.25rem;line-height:1.5em;word-spacing:1px}.timeline .timeline-block .timeline-content .icon{color:#fbffe5}.timeline .timeline-block:nth-child(odd){float:right}.timeline .timeline-block:nth-child(odd) .timeline-content .icon{margin-right:8px}.timeline .timeline-block:nth-child(even){float:left;direction:rtl}.timeline .timeline-block:nth-child(even) .timeline-content .icon{margin-left:8px}@media screen and (max-width: 768px){.timeline{width:95%}.timeline:before{left:8px;width:2px}.timeline .timeline-block{width:100%;margin-bottom:30px}.timeline .timeline-block:nth-child(odd){float:none}.timeline .timeline-block:nth-child(even){float:none;direction:ltr}.timeline .timeline-block:nth-child(even) .timeline-content .icon{margin-left:0;margin-right:8px}}

section.skills-section{padding:20px 0;background-color:#264653}section.skills-section .top-skills{display:flex;justify-content:center;align-items:center;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;flex-wrap:wrap}section.skills-section .top-skills .skill{border-radius:50%;min-width:100px;max-width:200px}@media screen and (max-width: 768px){section.skills-section .top-skills .skill{max-width:100px}}

.all-skills-section{background-color:#FFFFFF;padding:20px 0}.all-skills-section .title{color:#000}.all-skills-section .skills-container{max-width:1000px;margin:auto}

.cloud{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:20px;gap:20px;color:#76A329}.cloud :nth-child(2n+1){font-size:1em;color:#F42C04}.cloud :nth-child(3n+1){font-size:1.25em;color:#e15a97}.cloud :nth-child(4n+1){font-size:1.75em;color:#E2C044}.cloud :nth-child(5n+1){font-size:2em;color:#30ACCF}.cloud :nth-child(6n+1){font-size:2.25em;color:#9b287b}.cloud .word{margin:0 10px;align-items:center;display:flex;padding:0 1rem}

.about-section{min-height:100vh;display:flex;flex-direction:column;align-content:space-between;grid-gap:2rem;gap:2rem}.about-section .content{padding:0 3rem;flex-grow:1}.about-section .content h1{font-size:3rem;font-weight:100;margin-bottom:2rem}@media screen and (max-width: 768px){.about-section .content h1{font-size:2rem}}.about-section .content p{font-size:1.5rem;line-height:2.5rem;color:#777777}@media screen and (max-width: 768px){.about-section .content p{font-size:1rem}}.about-section .image{align-self:flex-end;width:-moz-available}.about-section .image img{width:100%}

.social-links{list-style:none;padding:0;display:flex;grid-gap:10px;gap:10px}.social-links li{padding:0}.social-links li a.github:hover{background-color:#181717}.social-links li a.linkedin:hover{background-color:#0077B5}.social-links li a.instagram:hover{background-color:#E4405F}.social-links li a.twitter:hover{background-color:#1DA1F2}.social-links li a{background-color:#888888;width:50px;height:50px;border-radius:25%;justify-content:center;display:flex;align-items:center}.social-links li a img{width:25px}

